

/* Main page */
.hero {
  background-color: #003434;
  background-size: cover;
}

.container {
  min-height: 300px;
  width: 1200px;
  margin: auto;
  padding-bottom: 100px;
}

.row {
  display: flex;
  align-content: center;
  justify-content: center;
  padding-top: 30px;
  margin-right: -25px;
  margin-left: -30px;
}

.collection {
  padding-bottom: 50px;
}

.button-blog {
  height: 100px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.columns {
  //min-height: 750px;
  height: 300px !important;
}



/* for further column styling in the blog area see utility classes */
.newsletter-container {
  min-height: 500px;
  width: 100%;
  padding-top: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.intro-header {
  padding-top: 50px; /* If you're making other pages, make sure there is 50px of padding to make sure the navbar doesn't overlap content! */
  padding-bottom: 50px;
  text-align: center;
  color: #f8f8f8;
  background: url(../images/intro-bg.jpg) no-repeat center center;
  background-size: cover;
}

.intro-message {
  position: relative;
  padding-top: 20%;
  padding-bottom: 20%;
}



/* Portfolio Section */
.content-section-a {
  padding: 50px 0;
  background-color: white;
}

.content-section-b {
  background-color: white;
  padding: 50px 0;
  border-top: 1px solid #e7e7e7;
  border-bottom: 1px solid #e7e7e7;
}

.section-heading {
  margin-bottom: 30px;
}

.section-heading-spacer {
  float: left;
  width: 200px;
  border-top: 3px solid #e7e7e7;
}

.col-lg-6 {
  padding: 0;
  margin-bottom: 60px;
}

.content-section-c {
  padding: 50px 0;
  background-color: white;
}

.tile-wrapper {
  height: auto;
  width: auto;
  border: 1px solid blue;
}

input[type=text] {
  background-color: #f8f8f8;
  color: black;
  width: 475px;
  height: 40px;
}

#box {
  background-color: #003434;
  color: white;
  width: 475px;
  height: 40px;
  padding: 0;
  margin: 0;
}

.network-name {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 400;
  letter-spacing: 2px;
  color: black;
}

/* End of main page */

/* Show */
.main-container {
  min-height: 1000px;
  width: 100%;
  background-color: #003434;
  }

.heading {
  min-height: 200px;
  width: 100%;
  padding-top: 50px;
  padding-left: 50px;
 }

#featured-image {
  border: 2px solid #F9F1DC;
  width: 39%;
}

.article-container {
  min-height: 800px;
  display: flex;
  padding-bottom: 20px;
  padding-left: 50px;
  flex: 0 0 100%;
}

.left-container {
  min-height: 800px;
  min-width: 65%;
}

.right-container {
  padding-left: 50px;
  min-height: 800px;
  min-width: 30%;
  display: flex;
  flex: 0 0 400px;
}

textarea {
  min-height: 100px;
  min-width: 50%;
  margin-bottom: 5px;
  outline: none;
  border: 0.1em solid #ccc;
  border-radius: 0.2em;
  background-color: #f9f1dc;
}

.category {
  background-color: none;
  border-radius: none;
  color: none;
  padding-top: 50px;
  padding-bottom: 20px;
  margin: 0;
  font-size: 12px;
  height: 24px;
  line-height: 16px;
}


.previous {
  padding-right: 200px;
}

.next {
  padding-left: 200px;
}

.heading {
  background-color: #003434;
  min-height: 300px;
  //border: 1px solid white;
  margin: 0;
  padding-left: 50px;
  padding-right: 20px;
}


/* Edit */
form {
  width: 520px;
  }

